@import '../../styles/variables';
@import '../../styles/mixins';

.header-appbar {
   background: $header-gradient !important;
   box-shadow: $shadow-sm !important;
   border: 0;
}

.header-toolbar {
   min-height: 70px;
}

.header-title {
  flex-grow: 1;
  font-weight: 700;
  text-align: center;
  letter-spacing: 1px;
}

.header-menu-btn {
   margin-right: $spacing-md;
   @include app-button($primary-color, $color-frost-white, $primary-color-hover);
}

.header-nav-btn {
   margin-left: $spacing-md;
   @include app-button(transparent, $color-frost-white, $primary-color-hover);
   background: transparent !important;
   box-shadow: none !important;

   &.active {
      border-bottom: 2px solid $color-frost-white;
   }
}

.header-drawer {
   .MuiPaper-root {
      border-radius: 8px;
      background: $input-bg;
   }
}

.header-drawer-list-item {
   border-radius: 8px;
   margin-bottom: $spacing-sm;

   &:hover {
      background: $hover-bg;
   }
}

.header-drawer-list-text {
  text-align: right;
  font-weight: 600;
}

.header-user {
  .header-username {
    color: $color-frost-white;
  }
}

.header-avatar {
  border: 2px solid rgba(255,255,255,0.4);
  font-weight: 600;
}

.header-drawer-user {
   background: linear-gradient(135deg, $primary-color, darken($primary-color, 8%));
   color: $color-frost-white;
   border-radius: 0 0 12px 12px;

   .header-username {
      font-weight: 600;
   }

   .header-avatar {
      box-shadow: 0 0 0 3px rgba(255,255,255,0.25);
   }
}
